import { Component, OnInit } from '@angular/core';
import { DismissReasons, DockedPanelContext } from '@bmc-ux/adapt-angular';
import { RowDataItem } from '@bmc-ux/adapt-table';
import { RX_ISSUES } from '../application-issues.constant';
import { IPlainObject } from '@helix/platform/shared/api';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';

@Component({
  selector: 'rx-issue-detail',
  templateUrl: './issue-details.component.html'
})
export class IssueDetailsComponent implements OnInit {
  issueFields: RowDataItem;
  fieldIds: IPlainObject;
  coreFieldIds: IPlainObject;

  constructor(private dockedPanelContext: DockedPanelContext) {}

  ngOnInit(): void {
    this.issueFields = this.dockedPanelContext.getData();
    this.fieldIds = RX_ISSUES.issuesRecordFieldIds;
    this.coreFieldIds = RX_RECORD_DEFINITION.coreFieldIds;
  }

  close(): void {
    this.dockedPanelContext.close(DismissReasons.CLOSE_BTN);
  }
}
